* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Quicksand;
}

h1, h2, h3, h4, h5, h6, ul, p, button {
  margin: 0;
  padding: 0;
}

a {
  color: unset;
  text-decoration: none;
}

li {
  list-style: none;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.header .nav__title :hover, .header .nav .covid :hover {
  color: #000;
  transition-duration: .3s;
}

.header .nav .globe__link :hover {
  fill: #000;
  transition-duration: .3s;
}

.header {
  background-color: #ffffff1a;
  border-bottom: 1px solid #fff;
  width: 100%;
  height: 52px;
}

.header .nav {
  flex-flow: row;
  justify-content: start;
  align-items: center;
  gap: 0;
  height: 52px;
  display: flex;
}

.header .nav__title {
  border-right: 1px solid #fff;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  width: 254px;
  height: 100%;
  display: flex;
}

.header .nav__title-link {
  text-transform: uppercase;
  color: #fff;
  font-size: 25px;
  font-weight: 500;
}

.header .nav__title-link span {
  font-weight: 700;
}

.header .nav__links {
  flex-grow: 1;
}

.header .nav__links .list {
  flex-flow: row;
  justify-content: start;
  align-items: center;
  gap: 0 5vw;
  padding: 0 0 0 100px;
  display: flex;
}

.header .nav__links .list__item-link {
  color: #fff;
  font-size: 22px;
  font-weight: 400;
  position: relative;
}

.header .nav__links .list__item-link:after {
  content: "";
  background-color: #000;
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  top: 100%;
  left: 50%;
  translate: -50%;
}

.header .nav__links .list__item-link:hover:after {
  width: 100%;
}

@media (width <= 768px) {
  .header .nav__links .list__item-link {
    font-size: 20px;
  }
}

@media (width <= 992px) {
  .header .nav__links .list {
    flex-flow: row;
    justify-content: center;
    align-items: center;
    gap: 0 4vw;
    padding: 0;
    display: flex;
  }
}

.header .nav .covid {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  height: 100%;
  padding: 0 30px;
  display: flex;
}

.header .nav .covid__link {
  color: #fff;
  font-size: 16px;
  font-weight: 700;
}

.header .nav .globe {
  flex-flow: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  width: 88px;
  display: flex;
}

.header .nav .globe__link-icon {
  fill: #fff;
  width: 28px;
  height: 28px;
}

body {
  background: #1f2ef7;
}
